import React, { Component } from 'react';
import Page from '../../components/Page';
import MathJax from '../../components/MathJax';
import Link from '../../components/Link';

const data = `
  arch、
  bash、bc、
  cal、
  date、df、dmesg、diff、
  env、exit、eval、
  free、
  git、
  hexdump、
  let、
  mount、man、
  openssl、
  passwd、
  reboot、
  sudo、source、sleep、screen、shutdown、su、
  usleep、umount、ulimit、
  which、whois、whoami、

  ps、top、kill、service、
  tar、xz、zip、unzip、unxz、
  realpath、xargs、
`.split('\n').join('').split('、').map(v=>v.trim()).filter(v=>v!='').sort()

export default function({ pageInfo }) {
  return (
    <Page
      title={pageInfo.title}
      page={pageInfo.page}
      maxPage={pageInfo.maxPage}
      category={pageInfo.category}
      data={data.join('、')}
    />
  )
}
